:root {
  --mainColor: #000000;
  --secondColor: #EBD96B;
}

body {
  font-family: 'Poppins', sans-serif;
}

.container {
  --width: 1720px;
  --padding: 30px;
  max-width: calc(var(--width) + var(--padding) * 2);
  margin: 0 auto;
  padding: 0 var(--padding);
}